









export default {
	data() {
		return {
			title:'',
			strings:''
		};
	},
	onLoad:function(e){
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				this.title=res.data.title;
				this.strings=res.data.content;
				uni.hideLoading(); 
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
